.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

*{
  margin: 0px;
  padding: 0px;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.heading{
  text-align: center;
}
@media(min-width:700px){
.Cards{
  height: 100px;
  width: 200px;
  border: 2px solid black;
  text-align: center;
  align-items: center;
  padding: 1%;
}
.Cards div{
  height: 40%;
}
.Cards h1{
  display: flex;
  justify-content: center;
}
.piecharts{
  display: flex;
  flex-wrap: wrap;
}
.bargraphs{
  display: flex;
  flex-wrap: wrap;
}
.contain{
  display: flex;
  height: 110vh;
  flex-basis: 50%;
}
}
@media(max-width:700px){
  .Cards{
    width: 170px;
    border: 2px solid black;
    text-align: center;
    padding: 1%;
  }
  .Cards div{
    height: 40%;
  }
  .Cards h1{
    display: flex;
    justify-content: center;
  }
}